body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center;font-family:'Bebas Neue', cursive;letter-spacing:2px;font-size:1.2em;background-color:none;color:white;box-shadow:0 6px 10px -4px #a00;padding:0}header a{all:unset;text-decoration:none;cursor:pointer;margin:0.5em 0 0.5em;padding:0.3em 0.8em 0.3em}header a:visited{color:unset}header .type{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-between;justify-content:space-between}header .type a:hover{color:#a00;border-bottom:5px solid #a00;transition:all 0.5s}header .type .search{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-align-items:center;align-items:center}.dropMenu{text-align:right;margin:5px}.dropMenu .dropdown{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:flex-end;justify-content:flex-end;-webkit-align-items:flex-end;align-items:flex-end}.dropMenu .dropdown .dropButton{margin:5px}.dropMenu .dropdown a{all:unset;color:#f4f4f4}.dropMenu .dropdown a:visited{all:unset;color:#f4f4f4}

.average{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;width:2.5em;height:1.5em;margin:0;padding:0;color:#f4f4f4}.average h6{font-size:0.8em;margin:0;padding:0}

.movie{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:flex-start;align-items:flex-start;padding:0;width:185px;max-width:200px;background-color:rgba(36,36,36,0.671);border-radius:6px;box-shadow:0 10px 15px 0 rgba(255,3,3,0.445);margin:0.8em;cursor:pointer}.movie .line1{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;width:100%}.movie .img-box{width:185px;height:274px}.movie .img-box img{object-fit:fill;height:100%;width:100%;margin:0;border:0;border-top-left-radius:6px;border-top-right-radius:6px;display:inline-block;box-sizing:border-box}.movie .titleMovie{width:100%;height:32px;margin-top:0.5em;margin-bottom:0.5em;vertical-align:middle;text-align:center;color:#f4f4f4}.movie h6{margin:0;padding-left:0.5em;margin-top:3px;-webkit-align-items:left;align-items:left;font-size:0.8em;color:#f4f4f4}

.popup{position:fixed;width:100%;height:100%;top:0;left:0;right:0;bottom:0;margin:auto;background-color:rgba(153,153,153,0.5)}.popup .popup_inner{position:absolute;left:20%;right:20%;top:20%;min-height:30vh;border-radius:15px;box-shadow:0 10px 15px 0 rgba(255,3,3,0.445);margin:0;padding:10px;background:black}@media only screen and (max-width: 1025px){.popup .popup_inner{top:15%;min-height:50vh}}@media only screen and (max-width: 768px){.popup .popup_inner{top:20%;left:10%;right:10%;min-height:50vh}}@media only screen and (max-width: 450px){.popup .popup_inner{top:0;left:0;right:0;height:100%}}.popup .popup_inner .closeButton{float:right;color:#f4f4f4;cursor:pointer}@media only screen and (max-width: 450px){.popup .popup_inner .closeButton{float:none}}.popup .popup_inner .content{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:center;justify-content:center;padding:5px;color:#f4f4f4;cursor:default}@media only screen and (max-width: 450px){.popup .popup_inner .content{-webkit-flex-direction:column;flex-direction:column}}.popup .popup_inner .content .box1{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;height:430px;margin:15px}.popup .popup_inner .content .box1 .poster img{height:350px;border-radius:6px}.popup .popup_inner .content .box1 h5{text-align:left;text-align:initial}.popup .popup_inner .content .box1 .data{width:233px;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center}.popup .popup_inner .content .box1 .budget{margin:0}.popup .popup_inner .content .box2{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:left;justify-content:left;-webkit-align-items:flex-start;align-items:flex-start;width:100%}.popup .popup_inner .content .box2 .info{width:100%}.popup .popup_inner .content .box2 .info .title{-webkit-justify-content:center;justify-content:center;border-bottom:5px solid #a00;width:100%;padding:15px 0 15px;font-size:1.2em}.popup .popup_inner .content .box2 .info .originalTitle{width:100%;margin:5px 0 5px;font-style:italic}.popup .popup_inner .content .box2 .info .tagline{margin:10px 0 5px;font-size:0.9em;color:#999}.popup .popup_inner .content .box2 .info img{height:30px}.popup .popup_inner .content .box2 .description{margin:0.5em 0 0.5em;font-weight:bold}.popup .popup_inner .content .box2 .overview{font-size:0.9em;margin:0;text-align:justify}@media only screen and (max-width: 450px){.popup .popup_inner .content .box2 .descriptionMovie{display:none}.popup .popup_inner .content .box2 .overview{display:none}}.popup .popup_inner .content .box2 .genres{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:center;align-items:center;width:100%}.popup .popup_inner .content .box2 .genres .genre{margin:5px;font-size:0.9em}

h3{font-family:'Bebas Neue', cursive;letter-spacing:2px;font-size:1em;color:#f4f4f4}

.pagesBar{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;color:white;margin:20px 0 20px}.pagesBar .moveButton{cursor:pointer}.pagesBar .moveButton:hover{color:#a00;-webkit-transform:scale(1.2);transform:scale(1.2);transition:all 0.5s}.pagesBar .numPage{margin:0 1em 0}

.movies{display:grid;grid-template-columns:repeat(5, 195px);-webkit-justify-content:center;justify-content:center;justify-items:center}@media only screen and (max-width: 1023px){.movies{grid-template-columns:repeat(4, 195px)}}@media only screen and (max-width: 768px){.movies{grid-template-columns:repeat(3, 195px)}}@media only screen and (max-width: 450px){.movies{grid-template-columns:repeat(1, 195px)}}

.search-box{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.search-box .searchTitle{margin:25px 0 0;color:#f4f4f4;font-family:'Bebas Neue', cursive;letter-spacing:1.5px;font-weight:normal}.search-box .searchBar{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;background:#fff;width:650px;height:44px;margin:40px;padding-left:15px;padding-right:15px;border:1px solid transparent;border-radius:24px;box-shadow:0 2px 5px 1px rgba(0,0,0,0.16)}@media only screen and (max-width: 1023px){.search-box .searchBar{width:60%}}@media only screen and (max-width: 450px){.search-box .searchBar{width:80%}}.search-box .searchBar input{width:100%;height:30px;background-color:transparent;border:none;border-radius:0.5em;margin:0;padding:0;color:rgba(0,0,0,0.87);word-wrap:break-word;outline:none;height:34px;font-size:16px}.search-box .searchBar input :focus{border:2px solid #0B5351}.search-box .searchBar button{background-color:unset;border:unset;-webkit-appearance:none;appearance:none}.search-box .searchBar img{height:30px}.search-box .searchResults{margin:0 0 20px;color:#f4f4f4}.search-box .result{display:grid;grid-template-columns:repeat(5, 195px);-webkit-justify-content:center;justify-content:center}@media only screen and (max-width: 1023px){.search-box .result{grid-template-columns:repeat(4, 195px)}}@media only screen and (max-width: 768px){.search-box .result{grid-template-columns:repeat(3, 195px)}}@media only screen and (max-width: 450px){.search-box .result{grid-template-columns:repeat(1, 195px)}}

.home{margin:12% auto 20%;color:#f4f4f4;font-family:'Bebas Neue', cursive;letter-spacing:2px;font-weight:normal;position:relative}.home a{all:unset;margin-bottom:2em;cursor:pointer}.home a:visited{color:unset}.home h3{margin-top:6em}.home .latest{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:center;justify-content:center;-webkit-align-items:flex-end;align-items:flex-end}@media only screen and (max-width: 450px){.home .latest{-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}}.home .latest img{width:185px;border-radius:6px;margin:5px}.home .latest h4{margin:5px}

.App {
  text-align: center;
}

